import React from "react";
import "./styles.css";
import productImg from "./assets/product-img.webp";

const ProductCard = () => {
  return (
    <div className="card">
      <img src={productImg} className="card-img-top" />
      <div className="card-body">
        <p className="title-product">
          NIKE AJ1 Retro High White University Blue Jeans Sepatu Olahraga
        </p>

        <p className="price">Rp. 3.550.000</p>
        <div  className="dicount">
            <span className=""></span>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
